import React from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import {Link} from 'react-router-dom';
import '../pages/css/global_comp.css';
export const renderNavBar = () =>{
    return(
        <Navbar className = "navbar-custom" expand="lg">
            <Navbar.Brand href="/"><p className = "light-blue header">Yixin Xiao</p></Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto">
                    <Nav.Link tag={Link} to="/aboutMe"><p className = "white normal-text">About Me</p></Nav.Link>
                    <Nav.Link tag={Link} to="/portfolio"><p className = "white normal-text">Porfolio</p></Nav.Link>
                    <Nav.Link tag={Link} to="/research"><p className = "white normal-text">Research</p></Nav.Link>
                    <Nav.Link tag={Link} to="/contactMe"><p className = "white normal-text">Contact Me</p></Nav.Link>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    );
}
